<template>
  <div>
    <v-card class="tw-mb-2 tw-p-3 tw-relative">
      <v-row>
        <v-col cols="12">
          <v-text-field v-model="search" dense clearable hide-details="auto" outlined label="Qidirish" prepend-inner-icon="mdi-magnify"></v-text-field>
        </v-col>
      </v-row>
    </v-card>
    <v-card v-if="search && !COLLECTION_QUIZZES(search).length">
      <v-card-text>
        <div class="tw-flex tw-justify-center tw-items-center tw-text-lg tw-py-5">
          Natija topilmadi 🤷🏻‍♂️
        </div>
      </v-card-text>
    </v-card>
    <v-card class="tw-mb-2 tw-relative" v-for="(collection_quiz, index) in COLLECTION_QUIZZES(search)" :key="index">
